@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&family=Work+Sans:wght@100;200;300;400;500;600;700;800;900&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Style+Script&display=swap');

body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
    
}

@media only screen and (max-width: 768px) {
    .grid-cont {
        grid-template-columns: 100%;
        grid-column-gap: 0;
    }

    .img1 {
        height: 60px;
        width: 60px;
    }
}

.main{
    background-image: url('../src/assets/bg-iith.jpeg');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: 100% 100%;
}


.first {
    margin: 20px;
    padding: 20px;
    
}

.subone{
    text-align: center;
    color: rgb(31, 31, 116);
} 


.lmao {
    text-align: center;
    font-size: 50px;
}

.box {
    margin-top: 20px;
    border: 1px black;
    border-radius: 50px;
    padding: 20px;
}

.grid-cont {
    margin-top: 30px;
    display: grid;
    grid-template-columns: 30% 30% 30%;
    grid-gap: 20px;
    align-items: center;
    justify-content: space-evenly;
}

.img1 {
    display: block;
    margin-left: auto;
    margin-right: auto;
    height: 150px;
    width: 150px;
    justify-content: space-evenly;
    border-radius: 60px;
    filter: drop-shadow(10px 1px 6px #c5c5c5);
}

.title{
    text-align: center;
    margin: 10px 0;
    font-size: 20px;
    color: rgb(31, 31, 116);

} 


#subtitle {
    text-align: center;
    margin: 10px 0;
    font-size: 20px;
    color: #FDa172

}

#ind {
    background-color: #fff;
    border: 1px  black;
    border-radius: 100px;
    padding: 20px;
}

.kkkk{
    display: flex;
    justify-content: center;
    color: rgb(31, 31, 116);
}


.img2{
    display: block;
    margin-left: auto;
    margin-right: auto;
    height: 150px;
    width: 150px;
    justify-content: space-evenly;
    border-radius: 60px;

}


.lambda{
    margin-top: 30px;
    display: grid;
    grid-template-columns: 50% 50%;
    grid-gap: 20px;
    align-items: center;
    justify-content: space-evenly;
    filter: drop-shadow(10px 1px 6px #c5c5c5);
}